import { NextResponse } from "next/server";
import { getServerSession } from "next-auth";
import { authOptions } from "@/lib/auth"; // Assurez-vous que authOptions est bien exporté depuis ce chemin
import prisma from "@/lib/prisma";

// Type pour le layout, doit correspondre à celui utilisé dans le frontend
interface DashboardLayout {
  zoneLeft: "profile" | "stacks";
  zoneTopRight: "profile" | "stacks";
}

export async function GET(request: Request) {
  const session = await getServerSession(authOptions);
  if (!session?.user?.id) {
    return NextResponse.json({ error: "Unauthorized" }, { status: 401 });
  }

  try {
    const user = await prisma.user.findUnique({
      where: { id: session.user.id },
      select: { dashboardLayout: true },
    });

    if (user && user.dashboardLayout) {
      // Assurez-vous que le type correspond. Prisma stocke JSON comme `JsonValue`.
      return NextResponse.json(user.dashboardLayout as DashboardLayout);
    } else {
      // Si aucune disposition n'est trouvée, retourner null.
      // Le client décidera d'utiliser localStorage ou une valeur par défaut.
      return NextResponse.json(null, { status: 200 });
    }
  } catch (error) {
    console.error("Error fetching layout:", error);
    return NextResponse.json(
      { error: "Failed to fetch layout" },
      { status: 500 }
    );
  }
}

export async function PUT(request: Request) {
  const session = await getServerSession(authOptions);
  if (!session?.user?.id) {
    return NextResponse.json({ error: "Unauthorized" }, { status: 401 });
  }

  try {
    const layout = (await request.json()) as DashboardLayout;

    // Validation simple du format du layout
    if (
      !layout ||
      typeof layout.zoneLeft !== "string" ||
      typeof layout.zoneTopRight !== "string" ||
      (layout.zoneLeft !== "profile" && layout.zoneLeft !== "stacks") ||
      (layout.zoneTopRight !== "profile" && layout.zoneTopRight !== "stacks") ||
      layout.zoneLeft === layout.zoneTopRight
    ) {
      return NextResponse.json(
        { error: "Invalid layout data" },
        { status: 400 }
      );
    }

    await prisma.user.update({
      where: { id: session.user.id },
      data: { dashboardLayout: layout }, // Prisma gère la conversion en JsonValue
    });
    return NextResponse.json(
      { message: "Layout updated successfully" },
      { status: 200 }
    );
  } catch (error) {
    console.error("Error updating layout:", error);
    if (error instanceof SyntaxError) {
      // Erreur de parsing JSON
      return NextResponse.json(
        { error: "Invalid JSON format" },
        { status: 400 }
      );
    }
    return NextResponse.json(
      { error: "Failed to update layout" },
      { status: 500 }
    );
  }
}
